.loading-map{
    position: fixed;
    top:0;
    right: 0;
    background-color: black;
    color:white;
    width: 100vw;
    height: 100vh;
}

.search-container{
    position: fixed;
    top:20px;
    left: 20px;
    background-color: white ;
    z-index: 999;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
    width: 250px;
    padding:5px;
    border-radius: 5px;
}

.pointer{
    cursor:pointer;
}